$white: #ffffff;
$background: #1F2932;

$easy: #67E4AA;
$recovery: #829FD6;
$long: #6C50D8;
$tempo: #BE3BCE;
$race: #C0268B;
$interval: #F3946E;
$hill: #F3946E;

$out-of-range-color: darken($white, 50%) !default;
$out-of-range-bg-color: darken($background, 4%) !default;

$calendar-border: $white !default;
$cell-border: $white !default;

$border-color: #CCC !default;

$segment-width: percentage(1 / 7) !default;

$time-selection-color: white !default;
$time-selection-bg-color: rgba(0,0,0, .50) !default;
$date-selection-bg-color: rgba(0,0,0, .10) !default;


$event-bg: $recovery !default;
$event-border: darken($recovery, 10%) !default;
$event-outline: $recovery !default;
$event-color: $white !default;
$event-border-radius: 5px !default;
$event-padding: 2px 5px !default;
$event-zindex: 4 !default;

$btn-color: #fff !default;
$btn-bg: transparent !default;
$btn-border: #fff !default;

$current-time-color: $easy !default;

$rbc-css-prefix: rbc-i !default;

$today-highlight-bg: lighten($background, 20%) !default;
